import * as vscode from 'vscode';
import * as json from 'jsonc-parser';
import * as path from 'path';
import { Builder, By, WebDriver} from 'selenium-webdriver';


export class SelCommand {

	private driver: WebDriver;
	
	constructor(driver: WebDriver) {
		this.driver = driver
	}

	async getText(locator: string): Promise<string> {
		return await this.driver.findElement(this.getBy(locator)).getText()
	}

	isDisplayed(locator: string): string {
		return this.driver.findElement(this.getBy(locator)).isDisplayed()
	}

	isSelected(locator: string): string {
		return this.driver.findElement(this.getBy(locator)).isSelected()
	}

	async runBatchScript(batchScript: string){
		const AsyncFunction = Object.getPrototypeOf(async function(){}).constructor
		let commands = new AsyncFunction('driver', batchScript)      
        await commands(this.driver)
	}

	async highlight(locator: string): Promise<void> {
		let _flasher = ["",""]

		let js = "var flasher = [];" 
					+ " flasher[0] = arguments[0].style.outline;"
					+ " flasher[1] = arguments[0].style.background;"
					+ " arguments[0].style.outline='3px solid red';"
					+ " arguments[0].style.background = 'yellow';"
					+ " return flasher;";
		
		let revertJs = "var flasher = [];" 
				+ " flasher[0] = arguments[0].style.outline;"
				+ " flasher[1] = arguments[0].style.background;"
				+ " arguments[0].style.outline='" + _flasher[0] + "';"
				+ " arguments[0].style.background = '" + _flasher[1] + "';"
				+ " return flasher;";	
		let by = this.getBy(locator)
		let ele = await this.driver.findElement(by)
		for(let i=0; i<3; i++){
			_flasher = await this.driver.executeScript(js, ele);
			await this.driver.sleep(300)
			_flasher = await this.driver.executeScript(revertJs, ele);
			await this.driver.sleep(300)
		}
	}
	
	getBy(locator: string): string {
		return locator.trim().startsWith("//") || locator.trim().startsWith(".//") 
			? By.xpath(locator)
			: By.css(locator)
	}

}